<template>
  <div>
    <!--  面包屑导航区域  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>管理页面</el-breadcrumb-item>
    </el-breadcrumb>

    <!--  数据请求渲染表格  -->
      <el-row :gutter="20">
        <el-col :span="2">预留</el-col>
        <el-col :span="14">
          <el-table :data="userTableData" style="width: 100%" border fit>
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="name" label="姓名" width="80"></el-table-column>
            <el-table-column prop="birthday" label="生日" width="80"></el-table-column>
            <el-table-column prop="age" label="年龄" width="80"></el-table-column>
            <el-table-column prop="sex" label="性别" width="80"></el-table-column>
            <el-table-column prop="note" label="备注" width="100"></el-table-column>
            <el-table-column label="操作" width="180">
              <template v-slot="scope">
                <el-button size="mini" type="primary">修改</el-button>
                <el-button size="mini" type="danger">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>

      <!--  分页显示  -->
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="userTableData.currentPage"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="10"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalpage">
        </el-pagination>
      </div>

  </div>
</template>

<script>

export default {
  data () {
    return {
      // 查询条件
      queryInfo: {
        pagenum: 1,
        pagesize: 10
      },
      // 用户表格数据
      userTableData: [],
      // 数据的总条数
      totalpage: 100
    }
  },
  created () {
    this.getUserTableData()
  },
  methods: {
    // 获取用户表格数据
    async getUserTableData () {
      const { data: res } = await this.$http.get('userList', {
        params: this.queryInfo
      })
      this.totalpage = res.data.total
      this.userTableData = res.data.user
      console.log(this.userTableData)
    },
    // 分页--改变页面展示数量
    handleSizeChange (newPageSize) {
      this.queryInfo.pagesize = newPageSize
      this.getUserTableData()
    },
    // 分页--改变当前页
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserTableData()
    }
  }
}
</script>

<style lang="less" scoped>

</style>
