<template>
  <!-- 顶部导航 -->
  <div class="header">
    <el-menu mode="horizontal" :default-active="activePath" router>
      <!-- 菜单 -->
      <el-menu-item v-for="item in menu" :index="item.path" :key="item.id"
                    @click="saveNavState(item.path)">
        {{item.name}}
      </el-menu-item>
    </el-menu>

    <!-- 登陆显示 -->
    <!-- 成功登陆 -->
    <div>
      <div class="loginBox" v-if="loginStatus">
        <div>
          <el-button size="mini" type="danger" @click="signOutBtn()">退出登陆</el-button>
        </div>
      </div>
      <!-- 未登陆 -->
      <div class="loginBox" v-else>
        <div>
          <el-button size="mini" @click="loginBtn()">登陆</el-button>
          <el-button size="mini" @click="registerBtn()" type="primary">注册</el-button>
        </div>
      </div>
    </div>
  </div>


</template>

<script>
export default {
  name: 'Nav',
  data () {
    return {
      // 导航菜单数据
      menu: [{ name: '主页', path: '/' },
        { name: '成员管理', path: '/manage' }
      ],
      // 导航激活选项
      activePath: '/welcome',
      // 控制登陆框的显示状态
      loginStatus: false
    }
  },
  created () {
    this.getLoginStatus()
    this.activePath = window.sessionStorage.getItem('active')
  },
  methods: {
    // 保存当前菜单状态
    saveNavState (active) {
      window.sessionStorage.setItem('active', active)
      this.activePath = active
    },
    // 检测当前用户是否登陆
    getLoginStatus () {
      let token = window.sessionStorage.getItem('token')
      console.log(token ? true : false)
      this.loginStatus = token ? true : false
    },
    // 退出按钮处理函数
    signOutBtn () {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    // 登陆按钮处理函数
    loginBtn () {
      this.$router.push('/login')
    },
    // 注册按钮处理函数
    registerBtn () {
      this.$router.push('/register')
    }
  }
}
</script>

<style lang="less" scoped>
.el-menu {
  padding: 0 20px;
  height: 40px;
  width: 85%;

  .el-menu-item {
    height: 40px;
    line-height: 40px;
  }
}

.loginBox {
  height: 100%;
  div {
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
}

.header {
  display: flex;
  justify-content: flex-start;
}
</style>
