<template>
  <!-- 轮播 -->
  <div class="swiper-container">
    <swiper ref="mySwiper" :options="swiperOptions">
      <swiper-slide v-for="(item, index) in imgUrl" :key="index"><img :src="`${item}`" alt=""/></swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <div class="swiper-button-next btn_ss"></div>
    <div class="swiper-button-prev btn_ss"></div>
  </div>

</template>

<script>
import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
  data () {
    return {
      swiperOptions: {
        spaceBetween: 30,
        centeredSlides: true,
        loop: true,
        effect: 'cube',
        autoplay: {
          delay: 2500,
          disableOnInteraction: false
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      // 图片url
      imgUrl: ['./img/x1.jpg', './img/x2.jpg', './img/x3.jpg', './img/x4.jpg']
      // 获取当前环境的根目录
      // publicPath: process.env.BASE_URL
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.$swiper
    }
  },
  mounted () {
    this.swiper.slideTo(3, 1000, false)
  },
  components: {
    Swiper,
    SwiperSlide
  },
  directives: {
    swiper: directive
  }
}
</script>

<style lang="less" scoped>
.swiper-container {
  width: 600px;
}

.swiper-slide {
  margin: 0 auto;
  img {
    width: 600px;
    height: 300px;
  }
}
</style>
