<template>
  <div class="register-container">
    <div style="height: 20px"></div>
    <el-card>
      <h2>Welcome</h2>
      <span>欢迎来到这里</span>

      <el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="80px"
               required>
        <!--  注册名称  -->
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <!--  密码  -->
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password" type="password"></el-input>
        </el-form-item>
        <!--  确认密码  -->
        <el-form-item label="确认密码" prop="rePassword">
          <el-input v-model="addForm.rePassword" type="password"></el-input>
        </el-form-item>
        <!--  安全邮箱  -->
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>

        <div class="btn-box">
          <el-button size="mini" type="primary" @click="submitBtn">注册</el-button>
          <el-button size="mini" type="info" @click="resetFormBtn">重置</el-button>
        </div>

        <!--  滑块验证  -->
        <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
          <slide-verify :l="42" :r="10" :w="310" :h="155" slider-text="向右滑动"
                        @success="onSuccess" @fail="onFail" @refresh="onRefresh">
          </slide-verify>
          <div>{{msg}}</div>
        </el-dialog>

      </el-form>
    </el-card>
    <div style="height: 20px"></div>
    <Footer/>
  </div>
</template>

<script>
import Footer from '../container/Footer'

import Vue from 'vue'
import SlideVerify from 'vue-monoplasty-slide-verify'

Vue.use(SlideVerify)

export default {
  data () {
    // 自定义校验规则 -- 再次确认密码
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.addForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    // 自定义校验规则 -- 邮箱格式校验
    var validateEmail = (rule, value, callback) => {
      // 正则验证规则
      let reg = /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/
      if (!reg.test(value)) {
        callback(new Error('邮箱验格式不正确'))
      } else {
        callback()
      }
    }
    return {
      // 表单数据
      addForm: {
        username: '',
        password: '',
        rePassword: '',
        email: ''
      },
      // 表单数据验证规则
      addFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
        ],
        rePassword: [
          { required: true, message: '请输入确认密码', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: validateEmail, trigger: 'blur' }
        ]
      },
      // 滑块验证信息
      msg: '',
      // 验证框显示
      dialogVisible: false
    }
  },
  methods: {
    // 滑块验证成功处理函数
    async onSuccess () {
      console.log('即将发送数据到后台')
      const { data: res } = await this.$http.post('adduser',this.addForm)
      console.log(res)
    },
    // 滑块验证失败
    onFail () {
      this.msg = ''
    },
    // 滑块刷新
    onRefresh () {
      this.msg = ''
    },
    // 表单重置按钮处理函数
    resetFormBtn () {
      console.log('sss')
      this.$refs.addFormRef.resetFields()
    },
    // 表单数据提交函数
    submitBtn () {
      // 提交按钮处理表单验证数据
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return false
        this.dialogVisible = true
      })
    }
  },
  components: {
    Footer
  }
}
</script>

<style lang="less" scoped>
.register-container {
  background-image: linear-gradient(to bottom right, #7A88FF, #7AFFAF);
}

.el-card {
  width: 90%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;

  .el-form {
    width: 430px;
    padding-top: 20px;

    .btn-box {
      width: 400px;
      display: flex;
      justify-content: flex-end;
      padding-bottom: 20px;
    }

    .slide-verify {
      margin: 0 auto;
    }
  }

  h2 {
    font-size: 50px;
  }
}
</style>
