<template>
  <div class="home-container">
    <canvas></canvas>
    <!--  登录框  -->
    <div class="login_box">

      <!--  表单验证 -->
      <el-form ref="loginFormRef" :model="loginForm" class="login_form"
               :rules="loginFormRules" label-width="0">
        <!--  用户名 -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="fa fa-user"></el-input>
        </el-form-item>
        <!--  密码 -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="fa fa-lock"></el-input>
        </el-form-item>
        <!--  按钮 -->
        <el-form-item class="buttons">
          <el-button size="mini" type="primary" @click="login">登陆</el-button>
          <el-button size="mini" type="info" @click="resetLoginForm">重置</el-button>
        </el-form-item>
      </el-form>

      <!--  服务连接  -->
      <el-row :gutter="20">
        <el-col :span="12"><a>忘记密码</a></el-col>
        <el-col :span="12"><a @click="toRegister">注册</a></el-col>
      </el-row>

    </div>
  </div>
</template>

<script>
import { myfun } from '../../assets/js/canvas'

export default {
  data () {
    return {
      // 表单数据
      loginForm: {
        username: '',
        password: ''
      },
      // 表单数据验证规则
      loginFormRules: {
        // 验证用户名字段
        username: [
          { required: true, message: '请输入账号', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
        ],
        // 验证密码字段
        password: [
          { required: true, message: '请输密码', trigger: 'blur' },
          { min: 4, max: 16, message: '长度在 6 到 16 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  created () {},
  methods: {
    // 重置表单内容
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    // 登陆操作
    login () {
      // 验证表单内容
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        // 验证成功,发送表单数据到服务器验证
        const { data: res } = await this.$http.post('test', this.loginForm)
        // 根据后台返回的数据进行验证
        if (res.meta.status !== 200) return this.$message.error('登陆失败')
        console.log(res)
        this.$message.success('登陆成功')
        // 成功登陆,根据服务器返回的信息,保存token,并跳转到主页
        window.sessionStorage.setItem('token', res.data.token)
        await this.$router.push('/')
      })
    },
    // 跳转到注册页面
    toRegister() {
      this.$router.push('/register')
    }
  },
  mounted () {
    // canvas图像
    myfun()
  }
}
</script>

<style lang="less" scoped>
.home-container {
  background-color: black;
  height: 100%;
}

.login_box {
  width: 450px;
  min-height: 300px;
  background-color: white;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.login_form {
  width: 100%;
  padding: 70px 20px 10px;
  box-sizing: border-box;

  .buttons {
    display: flex;
    justify-content: flex-end;
  }
}

.el-col {
  text-align: center;
}

</style>
